export default meta;
type Story = StoryObj<typeof Tags>;

// Template for interactive stories
const Template = (args: any) => {
  const [tags, setTags] = useState(args.defaultTags || []);

  return (
    <Tags
      {...args}
      defaultTags={tags}
      onChange={(newTags) => {
        setTags(newTags);
        action('onChange')(newTags);
      }}
      onTagAdd={(tag) => {
        action('onTagAdd')(tag);
      }}
      onTagRemove={(tag, index) => {
        action('onTagRemove')(tag, index);
      }}
      onBlur={(currentTags) => {
        action('onBlur')(currentTags);
      }}
    />
  );
};

